<template>
  <div class="mobile-home">
    <div class="wallet-info" v-if="walletAddress">
      <p>地址: {{ shortenAddress(walletAddress) }}</p>
      <p>余额: {{ balance }} ETH</p>
    </div>

    <van-button type="primary" size="large" @click="handleConnect">
      {{ walletAddress ? '已连接' : '连接钱包' }}
    </van-button>

    <van-button plain type="primary" size="large" @click="handleCreateWallet">
      创建新钱包
    </van-button>

    <van-dialog v-model="showMnemonic" title="请安全保存助记词" show-cancel-button>
      <div class="mnemonic-box">
        <p>{{ mnemonic }}</p>
        <van-button type="warning" size="small" @click="copyMnemonic">复制</van-button>
      </div>
      <p style="color: red; text-align: center;">请勿截图！请抄写到纸上并妥善保管</p>
    </van-dialog>
  </div>
</template>

<script>
import { connectWallet, getBalance, createWallet } from '@/utils/ethers';
import { Toast } from 'vant';

export default {
  data: () => ({
    walletAddress: '',
    balance: '0',
    showMnemonic: false,
    mnemonic: ''
  }),
  methods: {
    async handleConnect() {
      if (this.walletAddress) return;

      try {
        const signer = await connectWallet();
        this.walletAddress = await signer.getAddress();
        this.balance = await getBalance(this.walletAddress);
        Toast.success('钱包连接成功');
      } catch (err) {
        Toast.fail(err.message);
      }
    },

    handleCreateWallet() {
      const wallet = createWallet();
      this.mnemonic = wallet.mnemonic;
      this.showMnemonic = true;
      // 在实际应用中，应该让用户安全保存助记词后再进行后续操作
    },

    copyMnemonic() {
      navigator.clipboard.writeText(this.mnemonic);
      Toast.success('已复制到剪贴板');
    },

    shortenAddress(address) {
      return address ? `${address.slice(0,6)}...${address.slice(-4)}` : '';
    }
  }
};
</script>

<style scoped>
.mobile-home {
  padding: 20px;
  padding-top: 50px;
}

.wallet-info {
  background: #f7f8fa;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.mnemonic-box {
  padding: 20px;
  text-align: center;
}
</style>
